<template>
  <div class="Vuex_todulist">
    <h3></h3>
    <h3>--/</h3>
    <p>state:{{ $store.state.value }}</p>
    <button @click="$store.commit('ADD')"></button>
    <button @click="$store.commit('DEL')"></button>
    <hr />
    <button @click="$store.commit('ADD_TWO', 2)"></button>
    <button @click="$store.commit('DEL_TWO', 2)"></button>
    <hr />
    <button @click="$store.dispatch('yibuadd')"></button>
    <button @click="$store.dispatch('yibudel')"></button>
    <hr />
    <button @click="$store.dispatch('yibuadd_two', 2)"></button>
    <button @click="$store.dispatch('yibudel_two', 2)"></button>
    <pre>{{ storedemo1 }}</pre>
    <h3>--</h3>
    <p>{{ value }}</p>
    <button @click="ADD"></button>
    <button @click="DEL"></button>
    <hr />
    <button @click="ADD_TWO(2)"></button>
    <button @click="DEL_TWO(2)"></button>
    <hr />
    <button @click="yibuadd"></button>
    <button @click="yibudel"></button>
    <hr />
    <button @click="yibuadd_two(2)"></button>
    <button @click="yibudel_two(2)"></button>
    <pre>{{ storedemo2 }}</pre>
    <h3>vuex</h3>
    <pre>{{ store1 }}</pre>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      storedemo1: `
<p>state:{{ $store.state.value }}</p>
<button @click="$store.commit('ADD')"></button>
<button @click="$store.commit('DEL')"></button>
<hr />
<button @click="$store.commit('ADD_TWO', 2)"></button>
<button @click="$store.commit('DEL_TWO', 2)"></button>
<hr />
<button @click="$store.dispatch('yibuadd')"></button>
<button @click="$store.dispatch('yibudel')"></button>
<hr />
<button @click="$store.dispatch('yibuadd_two', 2)"></button>
<button @click="$store.dispatch('yibudel_two', 2)"></button>`,
      store1: `
state: {
    value: 0
  },
mutations: {
    ADD(state) {
      state.value++;
    },
    DEL(state) {
      state.value--;
    },
    ADD_TWO(state, index) {
      state.value += index;
    },
    DEL_TWO(state, index) {
      state.value -= index;
    }
  },
  actions: {
    yibuadd({ commit }) {
      commit('ADD');
    },
    yibudel({ commit }) {
      commit('DEL');
    },
    yibuadd_two({ commit }, index) {
      commit('ADD_TWO', index)
    },
    yibudel_two({ commit, state }, index) {//state
      commit('DEL_TWO', index)
    }
  }`,
      storedemo2: `
//html
<p>{{ value }}</p>
<button @click="ADD"></button>
<button @click="DEL"></button>
<hr />
<button @click="ADD_TWO(2)"></button>
<button @click="DEL_TWO(2)"></button>
<hr />
<button @click="yibuadd"></button>
<button @click="yibudel"></button>
<hr />
<button @click="yibuadd_two(2)"></button>
<button @click="yibudel_two(2)"></button>

//js
import { mapState, mapMutations, mapActions } from "vuex";

computed: {
   ...mapState(["value"]),
},
  methods: {
...mapMutations(["ADD", "DEL", "ADD_TWO", "DEL_TWO"]),
...mapActions(["yibuadd", "yibudel", "yibuadd_two", "yibudel_two"]),
  }`,
    };
  },
  computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD", "DEL", "ADD_TWO", "DEL_TWO"]),
    ...mapActions(["yibuadd", "yibudel", "yibuadd_two", "yibudel_two"]),
  },
};
</script>

