<template>
  <div class="Propsdemo">
    <h3></h3>
    <p>.vue</p>
    <p>,</p>
    <br />
    <h3>(props)</h3>
    <p>props </p>
    <pre>{{ fuzujian }}</pre>
    <p></p>
    <pre>{{ zuzujian }}</pre>
    <h3></h3>
    <p></p>
    <p>--</p>
    <pre>{{ hanshu }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fuzujian: `
//1.
import Props from "./components/props";
//2.html 
<Props name="" :age="18" :p="obj" :onck="ck"></Props>
//3.
obj: {
        name: "",
        age: 18,
      },
ck(name, age) {
      alert(""+this.name+""+this.age+"");
    }`,
      zuzujian: `
//
props: {
  //,
  ["name","age","p","onck"]

  //
  name:String,
  age:Number,
  p:Object,
  onck:Function

  //++
  name: { type: String, required: false, default: "" },
    age: { type: Number, required: false, default: 18 },
    p: {
      type: Object,
      required: false,
      default: () => {
        return { name: "", age: 18 };
      },
    },
    onck: {
      type: Function,
      required: false,
      default: () => {
        alert("");
        return 0
      },
    },
}
      `,
      hanshu: `
//  --
ck(name, age) {
      alert(""+this.name+""+this.age+"");
    }

//  --
<button @click="onck('', 19)"></button>
      `,
    };
  },
};
</script>

