<template>
  <div class="Promisedemo">
    <h3>Promise</h3>
    <p>Promise?Promise?</p>
    <p>,,</p>
    <p>
      ,
    </p>
    <p>Promise,resolve()reject()</p>
    <p>resolve.then((data)=>{}) </p>
    <p>reject.catch((err)=>{}) </p>
    <pre>{{
      `
      //promiseshow: false

      new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("");
      } else {
        reject("");
      }
    })
      .then((data) => {
        console.log(data);
      })
      .catch((err) => {
        console.log(err);
      });`
    }}</pre>
    <p>,</p>
    <p>rejecterr,</p>
    <pre>{{
      `new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("1");
      } else {
        reject("1");
      }
    }).then((data) => {
      console.log(data);
      new Promise((resolve, reject) => {
        if (this.promiseshow_two) {
          resolve("2");
        } else {
          reject("2");
        }
      }).then((data) => {
        console.log(data);
        new Promise((resolve, reject) => {
          if (this.promiseshow) {
            resolve("3");
          } else {
            reject("3");
          }
        })
          .then((data) => {
            console.log(data);
          })
          .catch((err) => {
            console.log(err);
          });
      });
    });`
    }}</pre>
    <p>Promise.all </p>
    <pre>{{
      `var a = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("1");
      } else {
        reject("1");
      }
    });
    var b = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("2");
      } else {
        reject("2");
      }
    });
    var c = new Promise((resolve, reject) => {
      if (this.promiseshow) {
        resolve("3");
      } else {
        reject("3");
      }
    });
    Promise.all([a, b, c])
      //datadata 
      .then(([data1, data2, data3]) => {
        console.log(data1, data2, data3);
      })
      .catch((err) => {
        console.log(err);
      });`
    }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      promiseshow: true,
      promiseshow_two: false,
    };
  },
  mounted() {},
};
</script>

