<template>
  <div class="Routerdemo">
    <h3></h3>
    <p>import router from './router'</p>
    <p>new Vue({ router, store, render: h => h(App) }).$mount('#app')</p>
    <hr />
    <p></p>
    <p>src/router/index.js</p>
    <p></p>
    <p></p>
    <p>&lt;router-view>&lt;/router-view></p>
    <a style="margin-right: 1rem" href="./#/routerdemo/routerhome"></a
    ><a href="./#/routerdemo/about"></a>
    <router-view class="view"></router-view>
    <pre>{{
      `import Vue from 'vue' //vue
import VueRouter from 'vue-router' //vuerouter
import Home from '../views/Home.vue' //

Vue.use(VueRouter) //vueroutervue

const routes = [
  {
    path: '/', //
    component: Home //
  }
]

const router = new VueRouter({ //router,
  routes
})

export default router
`
    }}</pre>
    <p>()</p>
    <router-link style="margin-right: 1rem" to="/routerdemo/routerhome"
      ></router-link
    >
    <router-link to="/routerdemo/about"></router-link>
    <router-view class="view"></router-view>
    <pre>{{ demo }}</pre>
    <h3></h3>
    <p class="i">a,</p>
    <router-link to="/routerdemo/routerhome" tag="span"
      >span</router-link
    >
    <pre>{{ demo2 }}</pre>
    <p class="i">
      router-link-exact-active
      active-class="myclas"
    </p>
    <router-link
      active-class="myclas"
      style="margin-right: 1rem"
      to="/routerdemo/routerhome"
      ></router-link
    >
    <router-link active-class="myclas" to="/routerdemo/about"
      ></router-link
    >
    <router-view class="view"></router-view>
    <pre>{{ demo3 }}</pre>
    <p class="i"></p>
    <router-link to="/routerdemo/routerhome/?"></router-link>
    <pre>
&lt;router-link to="/routerdemo/routerhome/?">&lt;/router-link></pre
    >
    <h3></h3>
    <p>1.push</p>
    <pre>{{demo4}}</pre>
    <pre>{{demo5}}</pre>
    <p>2.replace</p>
    <pre>{{` router.push `}}</pre>
    <pre>{{demo6}}</pre>
    <p>go</p>
    <pre>{{demo7}}</pre>
    <p> keep-alive</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      demo: `<router-link to="/routerdemo/routerhome"></router-link>
<router-link to="/routerdemo/about"></router-link>`,
      demo2: `<router-link to="/routerdemo/routerhome" tag="span">span</router-link>`,
      demo3: `
<router-link active-class="myclas" style="margin-right: 1rem" to="/routerdemo/routerhome"></router-link>
<router-link active-class="myclas" to="/routerdemo/about"></router-link>
<router-view class="view"></router-view>`,
      demo4:`// 
router.push('home')

// 
router.push({ path: 'home' })

//   --,  /:userId 
router.push({ name: 'user', params: { userId: '123' }})

//  /register?plan=private
router.push({ path: 'register', query: { plan: 'private' }})`,
      demo5:` pathparams  query 
       name  path

const userId = '123'
router.push({ name: 'user', params: { userId }}) // -> /user/123
router.push({ path: /user/123 }) // -> /user/123
//  params 
router.push({ path: '/user', params: { userId }}) // -> /user`,
      demo6:`	
<router-link :to="..." replace>

router.replace(...)`,
    demo7:` history  window.history.go(n)



//  history.forward()
router.go(1)

//  history.back()
router.go(-1)

//  3 
router.go(3)

//  history 
router.go(-100)
router.go(100)`,
    };
  },
};
</script>

<style>
.view {
  color: green;
}
</style>