import axios from "axios";  //axios
import { baseURL } from './baseURL'  //baseURL

//
axios.defaults.baseURL = baseURL; //url
axios.defaults.timeout = 5000; //5
axios.defaults.headers.post['Content-Type'] = 'application/x-www-from-urlencoded' //post //

//
// --  token
axios.interceptors.request.use((config) => {
  config.token = '11111111111111111111111111';
  return config; //token
}, (error) => {
  return Promise.error(error); // Promise.error
})

//  --
axios.interceptors.response.use((response) => {
  console.log('res', response);
  if (response.status == 500) { //
    console.log('');
  }
  return response.data; //--data
}, (error) => {
  return Promise.error(error); // Promise.error
})

//
export default function http(type = 'GET', url, params = {}) {
  // type url params   Promise
  return new Promise((resolve, reject) => {
    let promise; //
    if (type.toUpperCase() === 'GET') {
      //get
      promise = axios({
        method: 'get',
        url,
        params,
      })
    } else if (type.toUpperCase() === 'POST') {
      //post
      promise = axios({
        method: 'post',
        url,
        data: params,
      })
    }
    //  resolve catch
    promise.then((res) => { resolve(res) }).catch((err) => { reject(err) });
  })
}
