<template>
  <div class="Definition">
    <h3></h3>
    <p>vueif show </p>
    <h4></h4>
    <p>,,,main.js</p>
    <p v-convert1="world"></p>
    <pre>{{ quanjudemo }}</pre>
    <h4></h4>
    <p></p>
    <p v-convert2="world"></p>
    <pre>{{ jubudemo }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      world: "hello world",
      quanjudemo: `
// main.js

Vue.config.productionTip = false
/*    */
// convert1-- el binding 
Vue.directive('convert1', (el, binding) => {
  //console.log(el, binding);
  el.textContent = binding.value.toUpperCase();
});

//
<p v-convert1="world"></p>

//data
world: "hello world"`,
      jubudemo: `
//  directives

directives: {
    /*  */
    convert2(el, binding) {
      el.textContent = binding.value.toUpperCase();
    },
}

//html
<p v-convert2="world"></p>

//data
 world: "hello world" `,
    };
  },
  methods: {},
  computed: {},
  mounted() {},
  directives: {
    /*  */
    convert2(el, binding) {
      el.textContent = binding.value.toUpperCase();
    },
  },
};
</script>

