<template>
  <div class="Computers">
    <h3>(computed)</h3>
    <li>data,,</li>
    <li>--,</li>
    <li>
      --
    </li>
    <li>--getter()--setter()</li>
    <li>
       (){} ,get,set
      :{get(){},set(){}}
    </li>
    <hr />
    <input type="text" v-model="firstName" /><br />
    <input type="text" v-model="lastName" /><br />
    :<input type="text" v-model="fullName" />
    <pre>{{ Computersdemo1 }}</pre>
    <h3>(set)</h3>
    <input type="text" v-model="firstName2" /><br />
    <input type="text" v-model="lastName2" /><br />
    :<input type="text" v-model="fullName2" />
    <pre>{{ Computersdemo2 }}</pre>
    <h3>watch</h3>
    <li> --</li>
    <li> </li>
    <input type="text" v-model="firstName3" /><br />
    <input type="text" v-model="lastName3" /><br />
    :<input type="text" v-model="fullName3" />
    <pre>{{ watchdemo }}</pre>
    <h3></h3>
    <p>?</p>
    <p>/</p>
    <p>,,</p>
    <hr />
    <input type="text" v-model="obj.model1" />: {{ obj.model }}
    <input type="text" v-model="obj2.model2" />: {{ obj.model }}
    <pre>{{ shenduwatch }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      obj: {
        model1: "",
      },
      obj2: {
        model2: "",
      },
      firstName: "",
      lastName: "",
      firstName2: "",
      lastName2: "",
      firstName3: "",
      lastName3: "",
      fullName3: "",
      Computersdemo1: `
//html
<input type="text" v-model="firstName" /><br />
<input type="text" v-model="lastName" /><br />
:<input type="text" v-model="fullName" />

//data
firstName: "",
lastName: "",

//computed
computed: {
    fullName: {
      get() {
        return this.firstName + this.lastName;
      },
    },
}`,
      Computersdemo2: `
//html
<input type="text" v-model="firstName2" /><br />
<input type="text" v-model="lastName2" /><br />
:<input type="text" v-model="fullName2" />

//data
firstName2: "",
lastName2: "",

//computed
      fullName2: {
      get() {
        //  
        if (!this.firstName2 && !this.lastName2) return;
        //  "--" 
        return this.firstName2 + "--" + this.lastName2;
      },
      set(value) {
        // -- 
        //--,-- 
        if (value.indexOf("--") != -1) {
          this.firstName2 = value.split("--")[0];
          this.lastName2 = value.split("--")[1];
        } else {
          this.firstName2 = this.lastName2 = "";
        }
      },
    }`,
      watchdemo: `
//html
<input type="text" v-model="firstName3" /><br />
<input type="text" v-model="lastName3" /><br />
:<input type="text" v-model="fullName3" />

//data
firstName3: "",
lastName3: "",
fullName3: "",

//watch
      watch: {
    firstName3(value) {
      if (value) {
        this.fullName3 = value + "--" + this.lastName3;
      }
    },
    lastName3(value) {
      if (value) {
        this.fullName3 = this.firstName3 + "--" + value;
      }
    },
    fullName3(value) {
      if (value.indexOf("--") != -1) {
        this.firstName3 = value.split("--")[0];
        this.lastName3 = value.split("--")[1];
      } else {
        this.fullName3 = "";
        this.firstName3 = this.lastName3 = "";
      }
    },
  }`,
      shenduwatch: `
 //
      //1.handler:,
      //2.deep:true/false,
      //3.immediate:true/false

<input type="text" v-model="obj.model1" />: {{ obj.model }}
<input type="text" v-model="obj2.model2" />: {{ obj.model }}

obj: {
        model1: "",
      },
      obj2: {
        model2: "",
      },

obj(value) {
      console.log("", value.model1);
    },
    obj2: {
      deep: true,
      immediate: true, //
      handler(value) {
        console.log("", value.model2);
      },
    },
`,
    };
  },
  methods: {},
  computed: {
    fullName: {
      get() {
        return this.firstName + this.lastName;
      },
    },
    fullName2: {
      get() {
        if (!this.firstName2 && !this.lastName2) return;
        return this.firstName2 + "--" + this.lastName2;
      },
      set(value) {
        if (value.indexOf("--") != -1) {
          this.firstName2 = value.split("--")[0];
          this.lastName2 = value.split("--")[1];
        } else {
          this.firstName2 = this.lastName2 = "";
        }
      },
    },
  },
  watch: {
    firstName3(value) {
      if (value) {
        this.fullName3 = value + "--" + this.lastName3;
      }
    },
    lastName3(value) {
      if (value) {
        this.fullName3 = this.firstName3 + "--" + value;
      }
    },
    fullName3(value) {
      if (value.indexOf("--") != -1) {
        this.firstName3 = value.split("--")[0];
        this.lastName3 = value.split("--")[1];
      } else {
        this.fullName3 = "";
        this.firstName3 = this.lastName3 = "";
      }
    },

    obj(value) {
      console.log("", value.model1);
    },
    obj2: {
      //
      //1.handler:,
      //2.deep:true/false,
      //3.immediate:true/false
      deep: true,
      immediate: true, //
      handler(value) {
        console.log("", value.model2);
      },
    },
  },
};
</script>

<style>
</style>