<template>
  <div class="Anli">
    <h3></h3>
    <dir @click="chufa">
      <button></button><button></button><button></button>
    </dir>
    <hr />
    <h3></h3>
    <input type="text" v-model="text_filter" /> <br /><br />
     : <input type="text" v-model="bind.name" /> <select
      v-model="bind.sex"
    >
      <option value=""></option>
      <option value=""></option></select
    >: <input type="text" v-model="bind.age" />:
    <input type="text" v-model="bind.phone" /><button @click="add"></button>
    <ul>
      <li v-for="(item, index) in filter_shuju" :key="'arr' + index">
        {{ index + 1 }}) : {{ item.name }} : {{ item.sex }} :
        {{ item.age }} :{{ item.phone }}
        <button @click="del(index)"></button>
        <button @click="set(index)"></button>
      </li>
    </ul>
    <pre>{{ demo_html }}</pre>
    <pre>{{ demo_js_data }}</pre>
    <pre>{{ demo_js_com }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shuju: [
        {
          name: "",
          sex: "",
          age: 18,
          phone: "18788888888",
        },
        {
          name: "",
          sex: "",
          age: 19,
          phone: "18788888888",
        },
        {
          name: "",
          sex: "",
          age: 17,
          phone: "18788888888",
        },
      ],
      text_filter: "",
      flag: 0,
      bind: {
        name: "",
        sex: "",
        age: null,
        phone: "",
      },
      demo_html: `
//html
<h3></h3>
    <dir @click="chufa">
      <button></button><button></button><button></button>
    </dir>
    <hr />
    <h3></h3>
    <input type="text" v-model="text_filter" /> <br /><br />
     : <input type="text" v-model="bind.name" /> <select
      v-model="bind.sex"
    >
      <option value=""></option>
      <option value=""></option></select
    >: <input type="text" v-model="bind.age" />:
    <input type="text" v-model="bind.phone" /><button @click="add"></button>
    <ul>
      <li v-for="(item, index) in filter_shuju" :key="'arr' + index">
        {{ index + 1 }}) : {{ item.name }} : {{ item.sex }} :
        {{ item.age }} :{{ item.phone }}
        <button @click="del(index)"></button>
        <button @click="set(index)"></button>
      </li>
    </ul>
    <pre>{{ demo_html }}</pre>
    <pre>{{ demo_js_data }}</pre>
    <pre>{{ demo_js_com }}</pre>`,
      demo_js_data: `
//data
shuju: [
        {
          name: "",
          sex: "",
          age: 18,
          phone: "18788888888",
        },
        {
          name: "",
          sex: "",
          age: 19,
          phone: "18788888888",
        },
        {
          name: "",
          sex: "",
          age: 17,
          phone: "18788888888",
        },
      ],
      text_filter: "",
      flag: 0,
      bind: {
        name: "",
        sex: "",
        age: null,
        phone: "",
      },
`,
      demo_js_com: `
 computed: {
    filter_shuju() {
      // 1.   
      let { shuju, text_filter, flag } = this;

      // 2. return arr

      let arr = [...shuju]; //,

      // 3. 
      if (text_filter.trim()) {
        //text_filter.trim() -- 
        arr = shuju.filter((value) => {
          // name 
          return value.name.indexOf(text_filter) != -1;
        });
      }
      //4. flag
      if (flag) {
        //0,
        //--a-b b-a
        arr.sort((a, b) => {
          if (flag == 1) {
            return a.age - b.age;
          } else if (flag == 2) {
            return b.age - a.age;
          }
        });
      }
      //console.log(flag);
      return arr;
    },
  },
  methods: {
    chufa(e) {
      if (e.target.innerText == "") {
        this.flag = 0;
      } else if (e.target.innerText == "") {
        this.flag = 1;
      } else if (e.target.innerText == "") {
        this.flag = 2;
      }
    },
    add() {
      this.shuju.push(this.bind);
    },
    del(index) {
      this.shuju.splice(index, 1);
    },
    set(index) {
      this.shuju[index].name = prompt("");
    },
  },`,
    };
  },
  computed: {
    filter_shuju() {
      // 1.   
      let { shuju, text_filter, flag } = this;

      // 2. return arr

      let arr = [...shuju]; //,

      // 3. 
      if (text_filter.trim()) {
        //text_filter.trim() -- 
        arr = shuju.filter((value) => {
          // name 
          return value.name.indexOf(text_filter) != -1;
        });
      }
      //4. flag
      if (flag) {
        //0,
        //--a-b b-a
        arr.sort((a, b) => {
          if (flag == 1) {
            return a.age - b.age;
          } else if (flag == 2) {
            return b.age - a.age;
          }
        });
      }
      //console.log(flag);
      return arr;
    },
  },
  methods: {
    chufa(e) {
      if (e.target.innerText == "") {
        this.flag = 0;
      } else if (e.target.innerText == "") {
        this.flag = 1;
      } else if (e.target.innerText == "") {
        this.flag = 2;
      }
    },
    add() {
      this.shuju.push(this.bind);
    },
    del(index) {
      this.shuju.splice(index, 1);
    },
    set(index) {
      this.shuju[index].name = prompt("");
    },
  },
  watch: {},
};
</script>

<style>
ul {
  list-style: none;
}
</style>