<template>
  <div class="Vmodel">
    <h1>v-model</h1>
    <hr />
    <h3></h3>
    <input type="text" v-model="text" />
    <p>:{{ text }}</p>
    <pre>{{ textdemo }}</pre>
    <h3></h3>
    <input type="checkbox" id="jack" value="Jack" v-model="checkedNames" />
    <label for="jack">Jack</label>
    <input type="checkbox" id="john" value="John" v-model="checkedNames" />
    <label for="john">John</label>
    <input type="checkbox" id="mike" value="Mike" v-model="checkedNames" />
    <label for="mike">Mike</label><br /><span>{{ checkedNames }}</span
    ><br />
    <input type="checkbox" v-model="checkedNames2" />value<br />{{
      checkedNames2
    }}
    <br />

    <pre>{{ fuxuank }}</pre>
    <h3></h3>
    <div id="example-4">
      <input type="radio" id="one" value="One" v-model="picked" />
      <label for="one">One</label>
      <br />
      <input type="radio" id="two" value="Two" v-model="picked" />
      <label for="two">Two</label>
      <br />
      <span>Picked: {{ picked }}</span>
      <pre>{{ radiodemo }}</pre>
    </div>
    <h3></h3>
    <div id="example-5">
      <select v-model="selected">
        <option disabled value=""></option>
        <option value="1">A</option>
        <option value="2">B</option>
        <option>C</option>
      </select>
      <span>Selected: {{ selected }}</span>
    </div>
    <pre>{{ selectdemo }}</pre>
    <h3>
      <strong class="o"
        ><a target="_block" href="https://cn.vuejs.org/v2/guide/forms.html"></a></strong
      >
    </h3>
    <pre>//{{gengduo}}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkedNames2: "",
      text: "",
      textdemo: `
 //html
<input type="text" v-model="text" />
<p>:{{ text }}</p>

//data
text: ""`,
      checkedNames: [],
      fuxuank: `
 //html  inputlabelid  value
<input type="checkbox" id="jack" value="Jack" v-model="checkedNames" />
<label for="jack">Jack</label>
<input type="checkbox" id="john" value="John" v-model="checkedNames" />
<label for="john">John</label>
<input type="checkbox" id="mike" value="Mike" v-model="checkedNames" />
<label for="mike">Mike</label>
<br />
<span>{{ checkedNames }}</span>

<input type="checkbox" v-model="checkedNames2" />value{{checkedNames2}

//data
checkedNames:[],
checkedNames2:''`,
      picked: "",
      radiodemo: `
//html inputlabelid value
<div id="example-4">
<input type="radio" id="one" value="One" v-model="picked" />
<label for="one">One</label>
<br />
<input type="radio" id="two" value="Two" v-model="picked" />
<label for="two">Two</label>
<br />
<span>Picked: {{ picked }}</span>
</div>

//data
picked:""`,
      selected: "",
      selectdemo: `
//html 
<div id="example-5">
<select v-model="selected">
<option disabled value=""></option>
<option value="1">A</option>
<option value="2">B</option>
<option>C</option>//value
</select>
<span>Selected: {{ selected }}</span>
</div>

//data
selected:''`,
      gengduo:`
<input
  type="checkbox"
  v-model="toggle"
  true-value="yes"
  false-value="no"
>

// 
vm.toggle === 'yes'
// 
vm.toggle === 'no'

<select v-model="selected">
    <!--  -->
  <option v-bind:value="{ number: 123 }">123</option>
</select>

// 
typeof vm.selected // => 'object'
vm.selected.number // => 123
      `,
    };
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

