<template>
  <div class="Pubsub">
    <h3>Pubsub</h3>
    <p>npm install --save pubsub-js</p>
    <p>import Pubsub from "pubsub-js"</p>
    <p>--</p>
    <p>
      ,,,,mounted
    </p>
    <pre>{{ info }}</pre>
  </div>
</template>

<script>
import Pubsub from "pubsub-js";
export default {
  data() {
    return {
      info: `
//,
demo() {
      //--delTodo 
      Pubsub.publish("delTodo", (fun = "123") => {
        return fun;
      });
    },

//,
  mounted() {
    Pubsub.subscribe("delTodo", (msg, token) => {
      //msg(delTode)
      //token
      this.del(token); //
    });
  },
      `,
    };
  },
};
</script>

