<template>
  <div class="Config">
    <h3></h3>
    <pre>
axios.defaults.baseURL = 'http://demo.itlike.com/web/xlmc/api'  --(url)

axios.defaults.timeout = 1000 --1000  1000

method --
  --method:'get',
  --method:'post',
url  --
  --url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001' 
  --url:'/homeApi/categoriesdetail/lk001' baseURL</pre
    >
    <h3></h3>
    <button @click="ceshi"></button>
    <p>
       src/http  --index.js()
      baseURL.js(url) api.js(api)
    </p>
    <span class="i">baseURL.js</span>
    <pre>{{
      `export const baseURL = "http://demo.itlike.com/web/xlmc/api"`
    }}</pre>
    <span class="i">api.js</span>
    <pre>{{
      `import http from './index'

// 
export const getHome = data => {
  return http('get', '/homeApi/categoriesdetail/lk001', data);
};`
    }}</pre>
    <span class="i">index.js</span>
    <pre>{{
      `import axios from "axios";  //axios
import { baseURL } from './baseURL'  //baseURL

//
axios.defaults.baseURL = baseURL; //url
axios.defaults.timeout = 5000; //5
axios.defaults.headers.post['Content-Type'] = 'application/x-www-from-urlencoded' //post //

//
// --  token
axios.interceptors.request.use((config) => {
  config.token = '11111111111111111111111111';
  return config; //token
}, (error) => {
  return Promise.error(error); // Promise.error
})

//  --
axios.interceptors.response.use((response) => {
  console.log('res', response);
  if (response.status == 500) { //
    console.log('');
  }
  return response.data; //--data
}, (error) => {
  return Promise.error(error); // Promise.error
})

//
export default function http(type = 'GET', url, params = {}) {
  // type url params   Promise
  return new Promise((resolve, reject) => {
    let promise; //
    if (type.toUpperCase() === 'GET') {
      //get
      promise = axios({
        method: 'get',
        url,
        params,
      })
    } else if (type.toUpperCase() === 'POST') {
      //post
      promise = axios({
        method: 'post',
        url,
        data: params,
      })
    }
    //  resolve catch
    promise.then((res) => { resolve(res) }).catch((err) => { reject(err) });
  })
}
`
    }}</pre>
    <span class="i"></span>
    <pre>{{
      `getHome({ name: "jay", age: 18 })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });`
    }}</pre>
  </div>
</template>

<script>
import { getHome } from "../../http/api";
export default {
  methods: {
    ceshi() {
      getHome({ name: "jay", age: 18 })
        .then((res) => {
          console.log("res", res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

