<template>
  <div class="Axiosdemo">
    <h3>axios</h3>
    <p>import axios from "axios";</p>
    <p><a target="_block" href="https://axios-http.com/zh/docs/req_config">https://axios-http.com/zh/docs/req_config</a></p>
    <p>axiosPromise then,catch</p>
    <button @click="getimg"></button>
    <pre>{{
      ` axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001")
           .then((res) => {console.log(res)})
           .catch((err) =>{console.log(err)})`
    }}</pre>
    <h3></h3>
    <button @click="axiosdemo1"></button>
    <pre>{{
      ` axios({
        method:'get',
        url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001',
      }).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);});`
    }}</pre>
    <h3>get</h3>
    <button @click="axiosdemo2"></button>
    <pre>{{ demo2 }}</pre>
    <h3>post</h3>
    <button @click="axiosdemo3"></button>
    <pre>{{ demo3 }}</pre>
    <h3>--</h3>
    <p>Promise.all </p>
    <p>axios.then((res)=>{}) </p>
    <button @click="axiosdemo4"></button>
    <pre>{{demo4}}</pre>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      demo2: `      //.get
       axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}})
         .then((res) => {console.log(res)})
         .catch((err) =>{console.log(err)});

      //
       axios({
       method:'get',
       url:'http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001',
       params:{name:'lisa',age:18},
       }).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);});`,
      demo3: `      //.data
      axios
        .post(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { data: { name: "", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
        
      //data
      axios({
        method: "post",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        data: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });`,
      demo4:`    var a =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}});

    var b =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}});

    var c =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}});

    axios.all([a,b,c]).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);})`
    };
  },
  methods: {
    getimg() {
      axios
        .get(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001"
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo1() {
      axios({
        method: "get",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo2() {
      //.get
      axios
        .get(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { params: { name: "", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      //
      axios({
        method: "get",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        params: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo3() {
      //.data
      axios
        .post(
          "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
          { data: { name: "", age: 18 } }
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
      //data
      axios({
        method: "post",
        url: "http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",
        data: { name: "lisa", age: 18 },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    axiosdemo4(){
    var a =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}})

    var b =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}});

    var c =  axios.get("http://demo.itlike.com/web/xlmc/api/homeApi/categoriesdetail/lk001",{params:{name:'',age:18}});

    axios.all([a,b,c]).then((res)=>{console.log(res);}).catch((err)=>{console.log(err);})
    },
  },
};
</script>

