<template>
  <div class="Filtration">
    <h3></h3>
    <p>,,</p>
    <h3></h3>
    <p>,main.js</p>
    <p>:{{ qian | money1 }}</p>
    <pre>{{ guolvdemo }}</pre>
    <h3></h3>
    <p></p>
    <p>:{{ qian | money2 }}</p>
    <pre>{{ jubudemo }}</pre>
    <h3>(moment)</h3>
    <p>npm install moment --save</p>
    <p>import moment from "moment"</p>
    <p>:{{ time | timeformat }}</p>
    <p>:{{ time | timeformat("HH:mm:ss") }}</p>
    <pre>{{ timeformat }}</pre>
    <h3></h3>
    <pre>{{ timesdemo }}</pre>
  </div>
</template>

<script>
import moment from "moment";
export default {
  data() {
    return {
      qian: 16854,
      guolvdemo: `
//main.js

/*     */
Vue.filter('money1', (value) => {
  return "" + Number(value).toFixed(2);
})

//html
<p>:{{ qian | money }}</p>

//data
 qian:16854`,
      jubudemo: `
 // 
  filters: {
    money2(value) {
      return "" + Number(value).toFixed(2);
    },
  },

//html
<p>:{{ qian | money2 }}</p>

//data
qian: 16854`,
      time: new Date(),
      timeformat: `
//html
<p>:{{ time | timeformat }}</p>
<p>:{{ time | timeformat("HH:mm:ss") }}</p>

//data
time: new Date(),

//filters
timeformat(value, format = "YYYY-MM-DD HH:mm:ss") {
//format  
return moment(value).format(format);},
      `,
      timesdemo: `
//mounted
mounted() {
    this.timedemo = setInterval(() => {
      this.time = new Date();
    }, 1000);
  },

//
destroyed() {
    clearInterval(this.timedemo);
  }`,
    };
  },
  methods: {},
  mounted() {
    this.timedemo = setInterval(() => {
      this.time = new Date();
    }, 1000);
  },
  destroyed() {
    clearInterval(this.timedemo);
  },
  computed: {},
  //
  filters: {
    money2(value) {
      return "" + Number(value).toFixed(2);
    },
    timeformat(value, format = "YYYY-MM-DD HH:mm:ss") {
      //format  
      return moment(value).format(format);
    },
  },
};
</script>

