<template>
  <div class="Classdemo">
    <h3>v-bind</h3>
    <li>v-bind( : ) vue</li>
    <hr />
    <a :href="href_demo"></a>
    <pre
      >{{ href }}
    </pre>
    <h3></h3>
    <p :class="one"></p>
    <p :class="{ class1: isone }"></p>
    <p :class="[one, 'class2']"></p>
    <p :class="[{ class1: isone }, 'class2']"></p>
    <pre>{{ bindclass }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      href_demo: "http://www.baidu.com",
      href: `
//html
<a :href="href_demo"></a>

// src namedata
<img :src="'/path/to/images/' + name">

//style sizedata
<div :style="{ fontSize: size + 'px' }"></div>
<div :style="[styleObjectA, styleObjectB]"></div>

//data
href_demo:"http://www.baidu.com"`,
      one: "class1",
      isone: true,
      bindclass: `
//html
//one
<p :class="one"></p>

//class1 true
//
<p :class="{ class1: isone }"></p>

// 
<p :class="[one, 'class2']"></p>

//
<p :class="[{ class1: isone }, 'class2']"></p>

//
<p :class="{class1 : 1==2 ? true : false}"></p>
<p :class="[1==2 ? one : 'class2']"></p>
    
//data
one: "class1",
isone: true,

//style
.class1 {
color: red;
}
.class2 {
  background-color: pink;
}
      `,
    };
  },
  methods: {},
  mounted() {},
  computed: {},
};
</script>

<style>
.class1 {
  color: red;
}
.class2 {
  background-color: pink;
}
</style>