<template>
  <div class="ClickDemo">
    <h3></h3>
    <li>vue,</li>
    <hr />
    <p v-show="isShow"></p>
    <button @click="isShow = !isShow"></button>
    <pre>{{ clickdemo }}</pre>
    <h3></h3>
    <p v-show="isShow2"></p>
    <button @click="fun()"></button>
    <pre>{{ fundemo }}</pre>
    <h3></h3>
    <button @click="fun2('')"></button>
    <pre>{{ fundemo2 }}</pre>
    <h3></h3>
    <button @click="fun3($event, '')"></button>
    <pre>{{ fundemo3 }}</pre>
    <h3></h3>
    <pre>
//vueon @click
<img src="https://1578460233.github.io/vue-js2/img/on.0a6ff889.png"></pre>
    <h3><strong class="o">()</strong></h3>
    .stop .prevent .capture .self .once .passive
    <a href="https://cn.vuejs.org/v2/guide/events.html"></a>
    <pre>{{ xiushifu }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true,
      isShow2: true,
      clickdemo: ` 
//html
<p v-show="isShow"></p>
<button @click="isShow = !isShow"></button>

//data
isShow: true`,
      fundemo: `
//html
<p v-show="isShow2"></p>
<button @click="fun()"></button>

//data
isShow2: true,

//methods
methods: {
  fun() {
      this.isShow2 = !this.isShow2;
  }
}`,
      fundemo2: `
//html
<button @click="fun2('')"></button>

//methods
methods: {
  fun2(hello) {
    alert(hello);
  },
}`,
      fundemo3: `
//html
<button @click="fun3($event, '')"></button>

//methods
fun(e, hello) {
  console.log(e);
  console.log(hello);
}`,
      xiushifu: `
<!--  -->
<a v-on:click.stop="doThis"></a>

<!--  -->
<form v-on:submit.prevent="onSubmit"></form>

<!--  -->
<a v-on:click.stop.prevent="doThat"></a>

<!--  -->
<form v-on:submit.prevent></form>

<!--  -->
<!--  -->
<div v-on:click.capture="doThis">...</div>

<!--  event.target  -->
<!--  -->
<div v-on:click.self="doThat">...</div>

<!--  -->
<a v-on:click.once="doThis"></a>

<!--  ()  -->
<!--  onScroll   -->
<!--  event.preventDefault()  -->
<div v-on:scroll.passive="onScroll">...</div>

<!--  key  Enter  vm.submit() -->
<input v-on:keyup.enter="submit">`,
    };
  },
  methods: {
    fun() {
      this.isShow2 = !this.isShow2;
    },
    fun2(hello) {
      alert(hello);
    },
    fun3(e, hello) {
      console.log(e);
      console.log(hello);
    },
  },
  mounted() {},
  computed: {},
};
</script>

<style>
</style>