<template>
  <div class="Vuex_getters">
    <h3>getters</h3>
    <p>Vuex-getters,vuex,</p>
    <hr />
    <h3>,</h3>
    <p>{{ $store.state.value }}</p>
    <p>{{ $store.getters.value_type }}</p>
    <button @click="$store.commit('ADD')"></button>
    <pre>{{ getters1 }}</pre>
    <h3>,</h3>
    <p>{{ value }}</p>
    <p>{{ value_type }}</p>
    <button @click="ADD"></button>
    <pre>{{ getters2 }}</pre>
    <h3>vuex</h3>
    <pre>{{ vuexzhong }}</pre>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      vuexzhong: `
state: {
    value: 0
},
mutations: {
 ADD(state) {
   state.value++;
 },
},
getters: {
    value_type(state) {
      if (state.value % 2 != 0) {
        return ''
      } else {
        return ""
      }

    }
  }`,
      getters1: `
<p>{{ $store.state.value }}</p>
<p>{{ $store.getters.value_type }}</p>
<button @click="$store.commit('ADD')"></button>`,
      getters2: `
//html
<p>{{ value }}</p>
<p>{{ value_type }}</p>
<button @click="ADD"></button>

//
import { mapState, mapMutations, mapGetters } from "vuex";
 computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD"]),
  }`,
    };
  },
  computed: {
    ...mapState(["value"]),
    ...mapGetters(["value_type"]),
  },
  methods: {
    ...mapMutations(["ADD"]),
  },
};
</script>

