<template>
  <div class="donghua">
    <h3></h3>
    <pre style="text-align: center"><img src="../assets/donghua.png" ></pre>
    <div id="example-1">
      <transition name="slide">
        <div v-if="show" class="guodu"></div>
      </transition>
      <button @click="show = !show"></button>
      <h3></h3>
      <pre>{{ guodudemo }}</pre>
      <h3></h3>
      <transition name="donghua">
        <div v-if="show" class="donghua2"></div>
      </transition>
      <button @click="show = !show"></button>
      <pre>{{ donghuademo }}</pre>
      <h3>(animate)</h3>
      <p>npm install animate.css</p>
      <p>import animate from "animate.css"</p>
      <transition
        enter-active-class="animate__animated animate__tada"
        leave-active-class="animate__animated animate__flash"
      >
        <div v-if="show" class="donghuaku"></div>
      </transition>
      <button @click="show = !show"></button>
      <pre>{{ donghuakudemo }}</pre>
    </div>
  </div>
</template>

<script>
import animate from "animate.css";
export default {
  data() {
    return {
      show: true,
      guodudemo: `
//html
<transition name="slide">
  <div v-if="show" class="guodu"></div>
</transition>
<button @click="show = !show"></button>

//data
show: true,

//css
.guodu {
  width: 100px;
  height: 100px;
  background-color: red;
}
/*   :*/
.slide-enter-to {
  opacity: 1;
}
/*  :2 */
.slide-leave-active,
.slide-enter-active {
  transition: all 2s;
}
/*  :,50px*/
.slide-enter,
.slide-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
      `,
      donghuademo: `
//html
<transition name="donghua">
  <div v-if="show" class="donghua2"></div>
</transition>
<button @click="show = !show"></button>

//data
show: true,

//css
.donghua2 {
  width: 100px;
  height: 100px;
  background-color: pink;
}
/*  */
.donghua-enter-active {
  animation: bounce 1s; //1
}
.donghua-leave-active {
  animation: bounce 1s reverse; //1 
}
@keyframes bounce {
  0% {
    transform: scale(0);
  }
  25% {
    transform: scale(0.1);
  }
  50% {
    transform: scale(0.3);
  }
  75% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}
      `,
      donghuakudemo: `
//html  , animate
<transition
    enter-active-class="animate__animated animate__tada"
    leave-active-class="animate__animated animate__flash"
  >
    <div v-if="show" class="donghuaku"></div>
  </transition>
  <button @click="show = !show"></button>

//data
 show: true,

//css
.guodu {
  width: 100px;
  height: 100px;
  background-color: red;
}
  `,
    };
  },
};
</script>

<style>
.donghuaku {
  width: 100px;
  height: 100px;
  background-color: pink;
}
.donghua2 {
  width: 100px;
  height: 100px;
  background-color: pink;
}
.guodu {
  width: 100px;
  height: 100px;
  background-color: red;
}
/*  */
.slide-enter-to {
  opacity: 1;
}
.slide-leave-active,
.slide-enter-active {
  transition: all 2s;
}
.slide-enter,
.slide-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
/*  */
.donghua-enter-active {
  animation: bounce 1s;
}
.donghua-leave-active {
  animation: bounce 1s reverse;
}
@keyframes bounce {
  0% {
    transform: scale(0);
  }
  25% {
    transform: scale(0.1);
  }
  50% {
    transform: scale(0.3);
  }
  75% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(1);
  }
}
</style>